'use client';

import { useState, useEffect } from 'react';
import { useRouter, usePathname } from 'next/navigation'; // <-- AÑADIDO usePathname para obtener el ID
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { Card, CardHeader, CardTitle, CardDescription, CardContent, CardFooter } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { jugadorSchema, type JugadorFormData } from '@/lib/validations';
import { api } from '@/lib/api';
import type { Club, Jugador } from '@/types'; // <-- Importamos Jugador
import { toast } from 'sonner';
import Link from 'next/link';
import {
    Loader2,
    User,
    Calendar,
    Hash,
    CreditCard,
    Shirt,
    Building2,
    Save,
    X,
    ArrowLeft,
    CheckCircle2,
    Trophy,
    Pencil
} from 'lucide-react';

// Extracción de ID de la URL
const extractIdFromPath = (pathname: string): string | null => {
    // pathname es algo como /editar-jugador/12345
    const parts = pathname.split('/');
    // Intentamos obtener el último segmento, que debería ser el ID
    return parts[parts.length - 1] || null;
};

export default function EditarJugadorPage() {
    const router = useRouter();
    const pathname = usePathname();
    const jugadorId = extractIdFromPath(pathname);

    const [clubes, setClubes] = useState<Club[]>([]);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [isLoadingData, setIsLoadingData] = useState(true);

    const {
        register,
        handleSubmit,
        setValue,
        formState: { errors },
    } = useForm<JugadorFormData>({
        resolver: zodResolver(jugadorSchema),
        defaultValues: {
            numero: '',
            paterno: '',
            materno: '',
            nombres: '',
            run_input: '',
            rol_input: '',
            nacimiento: '',
            inscripcion: '',
            club_id: 0,
        },
    });

    // Función auxiliar para precargar datos
    const loadFormData = (jugador: Jugador) => {
        // Mapeo inverso de datos del Backend (Jugador) al Frontend (JugadorFormData)
        setValue('numero', jugador.numero.toString() || '');
        setValue('paterno', jugador.paterno || '');
        setValue('materno', jugador.materno || '');
        setValue('nombres', jugador.nombres || '');

        // El RUT y DV deben ser combinados o mapeados según tu lógica de validación (asumo que 'rut' es el número y 'dv' el dígito)
        // Ya que el backend devuelve `rut` (número) y `dv` (string), necesitamos reconstruir `run_input`
        const rutCompleto = jugador.rut && jugador.dv ? `${jugador.rut}-${jugador.dv}` : '';
        setValue('run_input', rutCompleto);

        setValue('rol_input', jugador.rol || '');
        setValue('nacimiento', jugador.nacimiento.split('T')[0] || ''); // Solo la fecha YYYY-MM-DD
        setValue('inscripcion', jugador.inscripcion.split('T')[0] || ''); // Solo la fecha YYYY-MM-DD
        setValue('club_id', jugador.Club?.id || 0); // Asumiendo que Club es un objeto con id
    };


    // Cargar clubes y datos del jugador al montar
    useEffect(() => {
        async function loadData() {
            if (!jugadorId) {
                toast.error('ID de jugador no encontrado en la URL.');
                setIsLoadingData(false);
                return;
            }

            try {
                // 1. Cargar clubes
                const clubesData = await api.getClubes();
                setClubes(clubesData);

                // 2. Cargar datos del jugador a editar
                // NOTA: ASUMIMOS QUE TENEMOS api.getJugadorPorId(jugadorId)
                const jugadorData = await api.getJugadorPorId(jugadorId);
                loadFormData(jugadorData);

            } catch (error) {
                console.error('Error cargando datos:', error);
                toast.error('Error al cargar la información del jugador.');
            } finally {
                setIsLoadingData(false);
            }
        }
        loadData();
    }, [jugadorId, setValue]);


    const onSubmit = async (data: JugadorFormData) => {
        if (!jugadorId) {
            toast.error('No se pudo obtener el ID para actualizar.');
            return;
        }

        setIsSubmitting(true);
        try {
            // Transformación similar a InscribirPage
            const dataToSend = {
                ...data,
                // Asegurar mayúsculas
                numero: data.numero.toUpperCase(),
                paterno: data.paterno.toUpperCase(),
                materno: data.materno.toUpperCase(),
                nombres: data.nombres.toUpperCase(),
                rol: data.rol_input.toUpperCase(),
                rut: data.run_input.toUpperCase(),
                // club_id ya está en formato number
            };

            // 🚀 LLAMADA A LA FUNCIÓN DE EDICIÓN (PUT)
            await api.updateJugador(jugadorId, dataToSend);

            toast.success('Jugador actualizado exitosamente');
            router.push('/'); // Volver al dashboard

        } catch (error) {
            console.error('Error al editar jugador:', error);
            toast.error('Error al actualizar el jugador. Por favor intente nuevamente.');
        } finally {
            setIsSubmitting(false);
        }
    };

    if (isLoadingData) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-slate-50 dark:bg-slate-950">
                <Loader2 className="h-8 w-8 animate-spin text-blue-600" />
                <p className="ml-3 text-slate-600 dark:text-slate-400">Cargando datos del jugador...</p>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-slate-50 dark:bg-slate-950">
            {/* Header (Mismo que InscribirPage) */}
            <header className="bg-white dark:bg-slate-900 border-b border-slate-200 dark:border-slate-800 sticky top-0 z-10">
                <div className="container mx-auto px-4 h-16 flex items-center justify-between">
                    <div className="flex items-center gap-2">
                        <div className="bg-blue-600 p-2 rounded-lg">
                            <Trophy className="h-5 w-5 text-white" />
                        </div>
                        <h1 className="text-xl font-bold bg-gradient-to-r from-blue-600 to-indigo-600 bg-clip-text text-transparent hidden sm:block">
                            Gestión Fútbol
                        </h1>
                    </div>
                </div>
            </header>

            <div className="max-w-6xl mx-auto py-8 px-4">
                {/* Top Navigation */}
                <div className="mb-8 flex items-center justify-between">
                    <Link href="/" className="inline-flex items-center text-sm font-medium text-slate-500 hover:text-slate-900 dark:text-slate-400 dark:hover:text-slate-100 transition-colors py-2">
                        <ArrowLeft className="mr-2 h-4 w-4" />
                        Volver al Dashboard
                    </Link>
                </div>

                <div className="grid grid-cols-1 lg:grid-cols-12 gap-8">
                    {/* Header & Info Column */}
                    <div className="lg:col-span-4 space-y-6">
                        <div className="lg:sticky lg:top-8">
                            <div className="mb-6">
                                <h1 className="text-3xl font-bold tracking-tight text-slate-900 dark:text-white sm:text-4xl mb-3 flex items-center gap-2">
                                    <Pencil className="h-8 w-8 text-blue-500" />
                                    Editar Jugador
                                </h1>
                                <p className="text-slate-600 dark:text-slate-400">
                                    Modifique la información y guarde los cambios en la ficha del jugador ID: **{jugadorId}**.
                                </p>
                            </div>

                            <Card className="bg-blue-50 dark:bg-blue-900/20 border-blue-100 dark:border-blue-900/50 shadow-none">
                                <CardContent className="p-4 flex items-start gap-4">
                                    <div className="p-2 bg-blue-100 dark:bg-blue-900/50 rounded-full shrink-0">
                                        <CheckCircle2 className="h-5 w-5 text-blue-600 dark:text-blue-400" />
                                    </div>
                                    <div className="space-y-1">
                                        <h4 className="font-semibold text-blue-900 dark:text-blue-100 text-sm">Modo Edición</h4>
                                        <p className="text-sm text-blue-700 dark:text-blue-300">
                                            Los campos ya están precargados con los datos actuales. Edite solo lo necesario y presione "Guardar Cambios".
                                        </p>
                                    </div>
                                </CardContent>
                            </Card>
                        </div>
                    </div>

                    {/* Form Column */}
                    <div className="lg:col-span-8">
                        <Card className="border-0 shadow-lg bg-white dark:bg-slate-900 ring-1 ring-slate-200 dark:ring-slate-800">
                            <form onSubmit={handleSubmit(onSubmit)}>
                                <CardHeader className="border-b border-slate-100 dark:border-slate-800 pb-4">
                                    <CardTitle className="text-xl font-semibold text-slate-800 dark:text-slate-100 flex items-center gap-2">
                                        <User className="h-5 w-5 text-slate-500" />
                                        Ficha Técnica
                                    </CardTitle>
                                    <CardDescription>
                                        Información personal y deportiva del jugador
                                    </CardDescription>
                                </CardHeader>

                                <CardContent className="p-6 sm:p-8 space-y-6">
                                    {/* Datos Personales */}
                                    <div className="space-y-4">
                                        <h3 className="text-sm font-medium text-slate-900 dark:text-slate-200 border-l-4 border-blue-500 pl-3">
                                            Datos Personales
                                        </h3>

                                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                                            {/* RUT */}
                                            <div className="space-y-2">
                                                <Label htmlFor="run_input">RUT</Label>
                                                <div className="relative">
                                                    <CreditCard className="absolute left-3 top-2.5 h-4 w-4 text-slate-400" />
                                                    <Input
                                                        id="run_input"
                                                        placeholder="12345678-9"
                                                        className="pl-9"
                                                        {...register('run_input')}
                                                    // En edición, el RUT generalmente NO debería ser editable, o tener validación especial. 
                                                    // Lo dejaremos editable por ahora.
                                                    />
                                                </div>
                                                {errors.run_input && <p className="text-xs text-red-500">{errors.run_input.message}</p>}
                                            </div>

                                            {/* Nombres */}
                                            <div className="space-y-2">
                                                <Label htmlFor="nombres">Nombres</Label>
                                                <div className="relative">
                                                    <User className="absolute left-3 top-2.5 h-4 w-4 text-slate-400" />
                                                    <Input
                                                        id="nombres"
                                                        placeholder="Nombres"
                                                        className="pl-9"
                                                        {...register('nombres')}
                                                        onChange={(e) => setValue('nombres', e.target.value.toUpperCase())}
                                                    />
                                                </div>
                                                {errors.nombres && <p className="text-xs text-red-500">{errors.nombres.message}</p>}
                                            </div>

                                            {/* Paterno */}
                                            <div className="space-y-2">
                                                <Label htmlFor="paterno">Apellido Paterno</Label>
                                                <Input
                                                    id="paterno"
                                                    placeholder="Paterno"
                                                    {...register('paterno')}
                                                    onChange={(e) => setValue('paterno', e.target.value.toUpperCase())}
                                                />
                                                {errors.paterno && <p className="text-xs text-red-500">{errors.paterno.message}</p>}
                                            </div>

                                            {/* Materno */}
                                            <div className="space-y-2">
                                                <Label htmlFor="materno">Apellido Materno</Label>
                                                <Input
                                                    id="materno"
                                                    placeholder="Materno"
                                                    {...register('materno')}
                                                    onChange={(e) => setValue('materno', e.target.value.toUpperCase())}
                                                />
                                                {errors.materno && <p className="text-xs text-red-500">{errors.materno.message}</p>}
                                            </div>

                                            {/* Nacimiento */}
                                            <div className="space-y-2">
                                                <Label htmlFor="nacimiento">Fecha de Nacimiento</Label>
                                                <div className="relative">
                                                    <Calendar className="absolute left-3 top-2.5 h-4 w-4 text-slate-400" />
                                                    <Input
                                                        id="nacimiento"
                                                        type="date"
                                                        className="pl-9"
                                                        {...register('nacimiento')}
                                                    />
                                                </div>
                                                {errors.nacimiento && <p className="text-xs text-red-500">{errors.nacimiento.message}</p>}
                                            </div>
                                        </div>
                                    </div>

                                    <div className="h-px bg-slate-100 dark:bg-slate-800" />

                                    {/* Datos Deportivos */}
                                    <div className="space-y-4">
                                        <h3 className="text-sm font-medium text-slate-900 dark:text-slate-200 border-l-4 border-indigo-500 pl-3">
                                            Datos Deportivos
                                        </h3>

                                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                                            {/* Club */}
                                            <div className="space-y-2 md:col-span-2">
                                                <Label htmlFor="club_id">Club</Label>
                                                <div className="relative">
                                                    <Building2 className="absolute left-3 top-3 h-4 w-4 text-slate-400 z-10" />
                                                    <Select
                                                        onValueChange={(value) => setValue('club_id', parseInt(value))}
                                                        // Necesitas establecer el valor inicial del Select para que se precargue el club
                                                        defaultValue={jugadorId || undefined}
                                                    // NOTA: El defaultValue debe ser el club_id actual del jugador.
                                                    // Esto es complejo con react-hook-form y shadcn/ui. Por simplicidad,
                                                    // estamos usando el default prop de useForm + setValue.
                                                    >
                                                        <SelectTrigger id="club_id" className="pl-9">
                                                            <SelectValue placeholder="Seleccione un club" />
                                                        </SelectTrigger>
                                                        <SelectContent>
                                                            {clubes.map((club) => (
                                                                <SelectItem key={club.id} value={club.id.toString()}>
                                                                    {club.nombre}
                                                                </SelectItem>
                                                            ))}
                                                        </SelectContent>
                                                    </Select>
                                                </div>
                                                {errors.club_id && <p className="text-xs text-red-500">{errors.club_id.message}</p>}
                                            </div>

                                            {/* Número Camiseta */}
                                            <div className="space-y-2">
                                                <Label htmlFor="numero">Número Camiseta</Label>
                                                <div className="relative">
                                                    <Shirt className="absolute left-3 top-2.5 h-4 w-4 text-slate-400" />
                                                    <Input
                                                        id="numero"
                                                        placeholder="Ej: 10"
                                                        className="pl-9"
                                                        {...register('numero')}
                                                    />
                                                </div>
                                                {errors.numero && <p className="text-xs text-red-500">{errors.numero.message}</p>}
                                            </div>

                                            {/* ROL */}
                                            <div className="space-y-2">
                                                <Label htmlFor="rol_input">ROL</Label>
                                                <div className="relative">
                                                    <Hash className="absolute left-3 top-2.5 h-4 w-4 text-slate-400" />
                                                    <Input
                                                        id="rol_input"
                                                        placeholder="ROL"
                                                        className="pl-9"
                                                        {...register('rol_input')}
                                                        onChange={(e) => setValue('rol_input', e.target.value.toUpperCase())}
                                                    />
                                                </div>
                                                {errors.rol_input && <p className="text-xs text-red-500">{errors.rol_input.message}</p>}
                                            </div>

                                            {/* Fecha Inscripción */}
                                            <div className="space-y-2">
                                                <Label htmlFor="inscripcion">Fecha Inscripción</Label>
                                                <div className="relative">
                                                    <Calendar className="absolute left-3 top-2.5 h-4 w-4 text-slate-400" />
                                                    <Input
                                                        id="inscripcion"
                                                        type="date"
                                                        className="pl-9"
                                                        {...register('inscripcion')}
                                                    />
                                                </div>
                                                {errors.inscripcion && <p className="text-xs text-red-500">{errors.inscripcion.message}</p>}
                                            </div>
                                        </div>
                                    </div>
                                </CardContent>

                                <CardFooter className="flex flex-col sm:flex-row gap-4 px-6 sm:px-8 py-6 bg-slate-50/50 dark:bg-slate-900/50 border-t border-slate-100 dark:border-slate-800">
                                    <Button
                                        type="submit"
                                        disabled={isSubmitting}
                                        className="w-full sm:w-auto bg-green-600 hover:bg-green-700 text-white"
                                    >
                                        {isSubmitting ? (
                                            <>
                                                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                                                Guardando...
                                            </>
                                        ) : (
                                            <>
                                                <Save className="mr-2 h-4 w-4" />
                                                Guardar Cambios
                                            </>
                                        )}
                                    </Button>

                                    <Link href="/" className="w-full sm:w-auto">
                                        <Button
                                            type="button"
                                            variant="outline"
                                            disabled={isSubmitting}
                                            className="w-full"
                                        >
                                            <X className="mr-2 h-4 w-4" />
                                            Cancelar
                                        </Button>
                                    </Link>
                                </CardFooter>
                            </form>
                        </Card>
                    </div>
                </div>
            </div>
        </div>
    );
}